los usuarios entre su hogar y el lugar de trabajo.")])])],1)],1),(_vm.logged || _vm.desktopClient)?_c('div',[_c('section',{staticClass:"electricidad"},[_c('b-row',{attrs:{"md":"10","offset-md":"1"}},[_c('b-container',[_c('b-row',[_c('b-col',{attrs:{"sm":"8"}},[_c('h3',{attrs:{"id":"title-elec"}},[_vm._v("Electricidad")]),_c('p',{attrs:{"id":"electricidad"}},[_vm._v("En cuanto a la energía, los estudios de metabolismo urbano examinan los principales tipos, que incluyen combustibles, electricidad, radiación y calor, aunque no se puede ignorar la energía almacenada que utiliza la construcción y la gestión de alimentos y residuos (Chrysoulakis et al., 2013). Con respecto al metabolismo urbano, los flujos de energía pueden contribuir a comprender cómo las zonas rurales y urbanas los utilizan, y como administran su oferta y demanda de energía (Villarroel-Walker y Beck 2012). Para el caso específico de Bogotá, en cuanto a los indicadores del metabolismo urbano, este trabajo ha demostrado que la adopción de la tecnología fotovoltaica en la ciudad podría satisfacer incluso el 44% de la demanda de electricidad de los sectores estudiados y contribuir a una reducción de hasta 820,000 t de gases de efecto invernadero por año para el año 2050.")]),_c('b-row',[_c('div',{staticClass:"btn-elec"},[_c('b-col',{attrs:{"sm":"6"}},[_c('b-button',{attrs:{"id":"elec","to":"https://drive.google.com/file/d/1Ss0p0Uwtt_LZ2guY6_UUGooZhatqcYtN/view"}},[_vm._v("Acceder a los archivos de simulación")])],1)],1)]),_c('br')],1),_c('b-col',{staticClass:"elec-img",attrs:{"sm":"4"}},[_c('b-img',{staticClass:"d-none d-sm-none d-md-block",attrs:{"src":require("@/assets/electr_resident.jpg"),"fluid":"","alt":"Responsive image"}})],1)],1)],1)],1)],1),_c('section',{staticClass:"transporte"},[_c('b-row',{attrs:{"md":"10","offset-md":"1"}},[_c('b-container',[_c('b-row',[_c('b-col',{staticClass:"trans-img",attrs:{"sm":"4"}},[_c('b-img',{staticClass:"d-none d-sm-none d-md-block",attrs:{"src":require("@/assets/transport.jpg"),"fluid":"","alt":"Responsive image"}})],1),_c('b-col',{attrs:{"sm":"8"}},[_c('h3',{attrs:{"id":"title-transpor"}},[_vm._v("Transporte")]),_c('p',{attrs:{"id":"transporte"}},[_vm._v("Una cuestión fundamental es la relación entre el crecimiento poblacional, el desarrollo demográfico y la infraestructura del transporte público. En particular, la ciudad atraviesa un momento clave de decisión frente a posibilidad de incorporar el Metro como uno de sus principales sistemas de transporte, en contraposición con el sistema de transporte masivo actual Transmilenio, que funciona con base en buses articulados. Es latente la necesidad de responder bajo qué condiciones un sistema de transporte masivo puede invadir, expandirse en el mercado y coexistir, a largo plazo, con los sistemas de transporte ya establecidos en la ciudad. Este tipo de preguntas guarda estrecha relación con otras estudiadas desde la biología evolutiva y han permitido el desarrollo de las Dinámicas Adaptativas como un marco teórico matemático útil para estudiar estas cuestiones.")]),_c('b-row',[_c('div',{staticClass:"btn-transpo"},[_c('b-col',{attrs:{"sm":"6"}},[_c('b-button',{attrs:{"id":"transpo","to":"https://drive.google.com/file/d/1N64RkSErCIsNwxPvjSafz9eui4geahoz/view"}},[_vm._v("Acceder a los archivos de simulación")])],1)],1)]),_c('br')],1)],1)],1)],1)],1),_c('section',{staticClass:"estupefacien"},[_c('b-row',{staticClass:"p-3",attrs:{"md":"10","offset-md":"1"}},[_c('b-container',[_c('b-row',[_c('b-col',{attrs:{"sm":"8"}},[_c('h3',{attrs:{"id":"title-estu"}},[_vm._v("Estupefacientes")]),_c('p',{attrs:{"id":"estupefacientes"}},[_vm._v("El cannabis es la sustancia ilícita más utilizada en el mundo, lo que motiva estudios alrededor de este tema en particular lo que tiene que ver con su distribución. Se ha considerado la ciudad de Bogotá como la región de interés y sus 112 UPZ (Unidades de Planeamiento Zonal) como los nodos de un grafo, para estudiar un tipo de grafo denominado de Umbral geográfico. En cada nodo se ha analizado la dinamica de consumo mediante la introduccion de un sistema de ecuaciones diferenciales ordinarias y fundamentos epidemiológicos. Los resultados obtenidos provienen de la articulación de la teoría de redes y el sistema de ecuaciones diferenciales, dando lugar al establecimiento zonas de influencia de lo que en el documento se denominó nodo distribuidor")]),_c('b-row',[_c('div',{staticClass:"btn-estup"},[_c('b-col',{attrs:{"sm":"6"}},[_c('b-button',{attrs:{"id":"estup","to":"https://drive.google.com/file/d/16qbFgb3Y8bEkfkuVLU85IKEE7kDJaoEG/view"}},[_vm._v("Acceder a la simulación")])],1)],1)]),_c('br')],1),_c('b-col',{staticClass:"estupe-img",attrs:{"sm":"4"}},[_c('b-img',{staticClass:"d-none d-sm-none d-md-block",attrs:{"src":require("@/assets/estupe.jpg"),"fluid":"","alt":"Responsive image"}})],1)],1)],1)],1)],1)]):_vm._e()],1)}
