import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import axios from 'axios';
import store from '../store'
Vue.use(VueRouter)

const routes = [
  {
    path: '/metaurbog',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/metaurbog/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "about" */ '../views/Login.vue')
  },
  {
    path: '/metaurbog/users',
    name: 'Users',
    component: () => import(/* webpackChunkName: "about" */ '../views/Users.vue')
  },
  {
    path: '/metaurbog/modelos/estupefacientes',
    name: 'Drugs',
    component: () => import(/* webpackChunkName: "about" */ '../views/models/Drugs.vue'),
  },
  {
    path: '/metaurbog/modelos/solar',
    name: 'Solar',
    component: () => import(/* webpackChunkName: "about" */ '../views/models/Solar.vue'),
  },
  {
    path: '/metaurbog/modelos/transporte',
    name: 'Transport',
    component: () => import(/* webpackChunkName: "about" */ '../views/models/TransportPRO.vue'),
  },
  {
    path: '/metaurbog/red-integrantes',
    component: () => import("../views/Members.vue")
  },
  {
    path: '/metaurbog/ponencias',
    component: () => import("../views/Ponencias.vue")
  },
  {
    path: '/metaurbog/seminarios',
    component: () => import("../views/Seminarios.vue")
  },
]

const router = new VueRouter({
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  mode: 'history',
  routes
})

router.beforeEach((to, from, next) => {
  if (!process.env.VUE_APP_DESKTOP_CLIENT) {
    if (to.name == 'Drugs' && !store.getters['auth/loggedIn']) {
      next({ name: 'Login' })
    }

    if (to.name == 'Login' && store.getters['auth/loggedIn']) {
      next({ name: 'Home' })
    }

    if ((to.name == 'Home' || to.name == 'Drugs') && store.getters['auth/loggedIn']) {
      const headers = {
        Authorization: store.getters['auth/authToken']
      }
      axios.get('/validate-token', { 'headers': headers }).then(resp => {
        console.log(resp)
        next()
      }).catch(() => {
        store.dispatch('auth/logout')
      })
    } else {
      next()
    }
  } else {
    next()
  }
  
})


export default router
